import React, { Component } from "react";
import { Button } from "@material-ui/core";

class RevenueCenter extends Component {
  state = {
    events: [],
    shouldShowEventDialog: false,
    newEvent: null
  };

  constructor(props) {
    super(props);
    this.headerComponentRef = React.createRef();
  }

  componentDidMount() {
    this.updateCalendar();
  }

  updateCalendar = () => {
  };

  handleDialogClose = () => {
    this.setState({ shouldShowEventDialog: false });
    this.updateCalendar();
  };

  handleEventMove = event => {
    this.handleEventResize(event);
  };

  handleEventResize = event => {
  };

  openNewEventDialog = ({ action, ...event }) => {
    if (action === "doubleClick") {
      this.setState({
        newEvent: event,
        shouldShowEventDialog: true
      });
    }
  };

  openExistingEventDialog = event => {
    this.setState({
      newEvent: event,
      shouldShowEventDialog: true
    });
  };

  render() {
    return (
      <div className="m-sm-30">

      </div>
    );
  }
}

export default RevenueCenter;
